form {
    display: grid;
    grid-template-columns: 1fr;
    grid-column: 1 / -1;
}
input[type=text]:focus {
    border: 3px solid rgb(85, 85, 85);
}
form div {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-column: 1 / -1;
    width: 100%;
    margin: 10px;
}
select {
    width: 15vw;
}
.div {
    grid-column: 1 / -1;
    color: rgb(200,0,0);
}